<template>
  <div class="header">
    <van-icon class="left" name="arrow-left" @click="back" />
    <p><slot></slot></p>
    <template v-if="star && !loading">
      <van-icon class="right" name="star-o" @click="add" v-if="!bool" />
      <van-icon
        class="right"
        v-bind:class="{ active: collect }"
        name="star"
        @click="remove"
        v-else
      />
    </template>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "Header",
  props: ["loading"],
  computed: {
    ...mapState(["star", "collect", "currentBook"]),
    bool() {
      //小说是否已经收藏
      var b = false;
      if (this.collect.length) {
        this.collect.forEach((element) => {
          if (element.id == this.$route.query.id) {
            b = true;
          }
        });
      }
      return b;
    },
  },
  methods: {
    ...mapMutations(["removeCollect", "addCollect"]),
    back() {
      // this.$router.push({ name: "Store" });
      // if (this.$route.query.type) {
      //   this.$router.push({ name: "Store" });
      // }else {
      //   this.$router.back();
      // }
      this.$router.back();
    },
    add() {
      var { _id: id, cover: img, title } = this.currentBook;
      this.addCollect({ id, img, title });
    },
    remove() {
      this.removeCollect(this.$route.query.id);
    },
  },
};
</script>

<style lang="less" scoped>
.header {
  top: 0;
  color: white;
  width: 100%;
  font-size: 18px;
  background-color: #f1867a;
  height: 50px;
  line-height: 50px;
  // margin-bottom: 50px;
  text-align: center;
  letter-spacing: 3px;
  font-weight: bold;
  position: fixed;
  z-index: 10;
  .van-icon.left {
    position: absolute;
    font-size: 25px;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
  .van-icon.right {
    position: absolute;
    font-size: 25px;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
  .active {
    color: #ffd21e;
  }
}
</style>