<template>
  <div class="rank">
    <Header>排行榜</Header>
    <div class="empty"></div>
    <HomeNav
      class="hn"
      :titles="titles"
      :gender="type"
      @navigate-to="navigate"
    ></HomeNav>
    <div class="main">
      <van-sidebar v-model="activeKey" v-if="rankList">
        <van-sidebar-item
          v-for="(item, index) in lists"
          :key="index"
          :title="item.title"
        />
      </van-sidebar>
      <ul class="content">
        <van-skeleton
          title
          avatar
          :row="3"
          :loading="loading"
          avatar-shape="square"
        >
          <CateBookItem
            v-for="(item, index) in rankDetail"
            :key="index"
            :book="item"
          ></CateBookItem>
        </van-skeleton>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import HomeNav from "@/components/HomeNav.vue";
import CateBookItem from "@/components/CateBookItem.vue";
import Header from "@/components/Header.vue";

export default {
  name: "Rank",
  components: {
    HomeNav,
    CateBookItem,
    Header,
  },
  data() {
    return {
      type: "epub",
      activeKey: 0,
      titles: [
        {
          id: 0,
          name: "出版",
          type: "epub",
          path: "/rank?type=epub",
        },
        {
          id: 1,
          name: "男生",
          type: "male",
          path: "/rank?type=male",
        },
        {
          id: 2,
          name: "女生",
          type: "female",
          path: "/rank?type=female",
        },
      ],
      rankDetail: [],
      loading: true,
    };
  },
  computed: {
    ...mapState(["rankList"]),
    lists() {
      if (this.type == "epub") {
        return this.rankList.epub;
      } else if (this.type == "male") {
        return this.rankList.male;
      } else {
        return this.rankList.female;
      }
    },
  },
  created() {
    if (this.rankList) {
      if (sessionStorage.getItem(`${this.lists[this.activeKey]._id}`)) {
        var y = JSON.parse(
          sessionStorage.getItem(`${this.lists[this.activeKey]._id}`)
        );
        this.rankDetail = y;
        return;
      }
      this.getRankDetail();
      return;
    }

    this.axios.get("/rank-category").then((response) => {
      this.$store.commit("updateRankList", response.data);
      this.getRankDetail();
    });
  },
  watch: {
    activeKey() {
      this.loading = true;
      if (sessionStorage.getItem(`${this.lists[this.activeKey]._id}`)) {
        var y = JSON.parse(
          sessionStorage.getItem(`${this.lists[this.activeKey]._id}`)
        );
        this.rankDetail = y;
        return;
      }
      this.getRankDetail();
    },
    "$route.query.type"() {
      this.activeKey = 0;
      if (sessionStorage.getItem(`${this.lists[this.activeKey]._id}`)) {
        // console.log("get");
        var y = JSON.parse(
          sessionStorage.getItem(`${this.lists[this.activeKey]._id}`)
        );
        this.rankDetail = y;
        return;
      }
      this.getRankDetail();
    },
    rankDetail() {
      console.log("rankDetail");
      this.loading = false;
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.type = to.query.type || "epub";
    });
  },
  beforeRouteUpdate(to, from, next) {
    this.type = to.query.type || "epub";
    next();
  },
  methods: {
    navigate(n) {
      this.$router.replace(`/rank?type=${n}`).catch((err) => err);
    },
    //数据存到sessionStorage中
    getRankDetail() {
      this.axios
        .get(`/rank/${this.lists[this.activeKey]._id}`)
        .then((response) => {
          //   console.log("rankDetail", response.data.ranking.books);
          this.rankDetail = response.data.ranking.books;
          var x = JSON.stringify(response.data.ranking.books);
          sessionStorage.setItem(`${this.lists[this.activeKey]._id}`, x);
        });
    },
  },
};
</script>

<style lang="less" scoped>
.rank {
  .main {
    display: flex;
    height: calc(100vh - 140px);
    .van-sidebar {
      background-color: #f7f8fa;
      text-align: center;
      width: 100px;
    }
    .content {
      flex: 1;
      overflow-y: scroll;
    }
  }

  .empty {
    width: 100%;
    height: 50px;
  }
}
</style>